// src/components/gravity/ArtifactStrip.tsx
"use client";

import * as React from "react";

type Artifact = {
  title: string;
  note: string;
  tag: string;
};

const ARTIFACTS: Artifact[] = [
  {
    title: "The CTA wasn’t the problem",
    note: "People didn’t trust the next step. We changed the step, not the button.",
    tag: "decision design",
  },
  {
    title: "We removed 40% of the UI",
    note: "Less choice. More commitment. Drop-off fell without adding features.",
    tag: "reduction",
  },
  {
    title: "A modal that refuses to sell",
    note: "No pitch. Just a mirror. It filtered the wrong users instantly.",
    tag: "self-selection",
  },
  {
    title: "Micro-friction for honesty",
    note: "A tiny pause prevents fake intent. You get cleaner signals.",
    tag: "signal",
  },
];

export function ArtifactStrip() {
  return (
    <div className="rounded-3xl border border-white/10 bg-white/5 p-6 sm:p-7">
      <div className="flex items-center justify-between">
        <div className="text-[10px] font-mono uppercase tracking-[0.22em] text-[var(--grav-dim)]">
          artifacts
        </div>
        <div className="text-[10px] font-mono uppercase tracking-[0.22em] text-[var(--grav-dim)]">
          no gallery
        </div>
      </div>

      <div className="mt-4 space-y-3">
        {ARTIFACTS.map((a) => (
          <div
            key={a.title}
            className="rounded-2xl border border-white/10 bg-black/10 p-4 hover:bg-black/15 transition-colors"
          >
            <div className="flex items-center justify-between gap-3">
              <div className="text-sm font-semibold tracking-tight">{a.title}</div>
              <span className="text-[9px] font-mono uppercase tracking-[0.22em] text-[var(--grav-dim)]">
                {a.tag}
              </span>
            </div>
            <div className="mt-1 text-xs leading-relaxed text-[var(--grav-dim)]">
              {a.note}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
