// src/components/gravity/GravityStage.tsx
"use client";

import * as React from "react";
import { motion } from "framer-motion";
import { cn } from "@/lib/utils";

type Props = {
  reduced: boolean;
};

function usePointer() {
  const [p, setP] = React.useState({ x: 0.5, y: 0.2 });
  React.useEffect(() => {
    const onMove = (e: PointerEvent) => {
      const w = window.innerWidth || 1;
      const h = window.innerHeight || 1;
      setP({ x: e.clientX / w, y: e.clientY / h });
    };
    window.addEventListener("pointermove", onMove, { passive: true });
    return () => window.removeEventListener("pointermove", onMove);
  }, []);
  return p;
}

export function GravityStage({ reduced }: Props) {
  const p = usePointer();

  const glowX = reduced ? 50 : Math.round(p.x * 100);
  const glowY = reduced ? 18 : Math.round(p.y * 40);

  return (
    <section className="relative overflow-hidden">
      <div className="absolute inset-0">
        <div
          className="h-[52svh] w-full"
          style={{
            background:
              "radial-gradient(1200px 520px at 20% 10%, rgba(255,255,255,0.06), transparent 60%)," +
              "radial-gradient(900px 420px at 75% 30%, rgba(255,255,255,0.05), transparent 62%)," +
              "linear-gradient(to bottom, rgba(0,0,0,0.0), rgba(0,0,0,0.25))",
          }}
        />
        <div
          className="absolute inset-0 opacity-[0.55]"
          style={{
            background: `radial-gradient(900px 420px at ${glowX}% ${glowY}%, rgba(124, 58, 237, 0.14), transparent 60%)`,
          }}
        />
        <div className="absolute inset-0 grav-noise" />
      </div>

      <div className="relative mx-auto max-w-6xl px-6 pt-16 pb-12">
        <div className="flex items-center justify-between">
          <div className="inline-flex items-center gap-2 rounded-full border border-white/10 bg-white/5 px-3 py-1">
            <span className="h-1.5 w-1.5 rounded-full bg-[var(--grav-accent)]" />
            <span className="text-[10px] font-mono tracking-[0.22em] uppercase text-[var(--grav-dim)]">
              tatedonohoe.au
            </span>
          </div>

          <a
            href="#contact"
            className={cn(
              "text-[10px] font-mono tracking-[0.22em] uppercase",
              "text-[var(--grav-dim)] hover:text-[var(--grav-ink)] transition-colors",
            )}
          >
            CONTACT
          </a>
        </div>

        <div className="mt-14 max-w-3xl">
          <motion.h1
            initial={{ opacity: 0, y: 10, filter: "blur(6px)" }}
            animate={{ opacity: 1, y: 0, filter: "blur(0px)" }}
            transition={{ duration: 0.55, ease: [0.2, 0.8, 0.2, 1] }}
            className="text-[28px] sm:text-[40px] font-semibold tracking-tight leading-[1.05]"
          >
            I don’t build websites.
            <br />
            I build{" "}
            <span className="text-[var(--grav-ink)]/90">
              the way people decide.
            </span>
          </motion.h1>

          <motion.p
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.55, delay: 0.08, ease: [0.2, 0.8, 0.2, 1] }}
            className="mt-5 text-sm sm:text-base text-[var(--grav-dim)] leading-relaxed"
          >
            Most portfolios explain. This one behaves.
          </motion.p>

          <div className="mt-10 grid grid-cols-1 sm:grid-cols-3 gap-3">
            <Pill>Systems over screens</Pill>
            <Pill>Friction on purpose</Pill>
            <Pill>Evidence, not claims</Pill>
          </div>
        </div>
      </div>
    </section>
  );
}

function Pill({ children }: { children: React.ReactNode }) {
  return (
    <div className="rounded-2xl border border-white/10 bg-white/5 px-4 py-3">
      <div className="text-[10px] font-mono uppercase tracking-[0.22em] text-[var(--grav-dim)]">
        {children}
      </div>
    </div>
  );
}
