// src/components/gravity/CognitiveTrap.tsx
"use client";

import * as React from "react";
import { motion, AnimatePresence } from "framer-motion";

type Mode = "calm" | "hunting" | "impatient";

function clamp(n: number, a: number, b: number) {
  return Math.max(a, Math.min(b, n));
}

export function CognitiveTrap() {
  const [mode, setMode] = React.useState<Mode>("calm");
  const [score, setScore] = React.useState(0);

  React.useEffect(() => {
    let clicks = 0;
    let wheel = 0;
    let jitter = 0;

    let lastMove = { x: 0, y: 0, t: Date.now() };
    const onMove = (e: PointerEvent) => {
      const now = Date.now();
      const dt = now - lastMove.t || 1;
      const dx = e.clientX - lastMove.x;
      const dy = e.clientY - lastMove.y;
      const v = Math.sqrt(dx * dx + dy * dy) / dt; // px/ms
      // high-frequency tiny motion is "searching"
      if (v > 0.6 && dt < 40) jitter += 1;
      lastMove = { x: e.clientX, y: e.clientY, t: now };
    };

    const onClick = () => {
      clicks += 1;
    };

    const onWheel = () => {
      wheel += 1;
    };

    const tick = window.setInterval(() => {
      const s = clicks * 2 + wheel * 1 + jitter * 1;
      setScore((prev) => clamp(Math.round(prev * 0.65 + s * 0.35), 0, 100));

      // decay
      clicks = Math.max(0, clicks - 1);
      wheel = Math.max(0, wheel - 2);
      jitter = Math.max(0, jitter - 4);
    }, 600);

    window.addEventListener("pointermove", onMove, { passive: true });
    window.addEventListener("click", onClick, { passive: true });
    window.addEventListener("wheel", onWheel, { passive: true });

    return () => {
      window.clearInterval(tick);
      window.removeEventListener("pointermove", onMove);
      window.removeEventListener("click", onClick);
      window.removeEventListener("wheel", onWheel);
    };
  }, []);

  React.useEffect(() => {
    if (score >= 32) setMode("impatient");
    else if (score >= 14) setMode("hunting");
    else setMode("calm");
  }, [score]);

  return (
    <div className="rounded-3xl border border-white/10 bg-white/5 p-6 sm:p-7 relative overflow-hidden">
      <div className="absolute inset-0 pointer-events-none opacity-[0.7]">
        <div className="absolute -top-24 -right-24 h-64 w-64 rounded-full bg-[var(--grav-accent)]/10 blur-3xl" />
        <div className="absolute -bottom-24 -left-24 h-64 w-64 rounded-full bg-white/5 blur-3xl" />
      </div>

      <div className="relative">
        <div className="flex items-center justify-between">
          <div className="text-[10px] font-mono uppercase tracking-[0.22em] text-[var(--grav-dim)]">
            Interaction readout
          </div>
          <div className="text-[10px] font-mono uppercase tracking-[0.22em] text-[var(--grav-dim)]">
            signal {String(score).padStart(2, "0")}
          </div>
        </div>

        <div className="mt-4">
          <h3 className="text-lg font-semibold tracking-tight">
            You’re looking for proof.
          </h3>

          <AnimatePresence mode="popLayout">
            {mode === "calm" && (
              <motion.p
                key="calm"
                initial={{ opacity: 0, y: 6 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: -6 }}
                transition={{ duration: 0.22 }}
                className="mt-2 text-sm text-[var(--grav-dim)] leading-relaxed"
              >
                Cool. I’ll give you evidence. But not in the way portfolios do.
              </motion.p>
            )}

            {mode === "hunting" && (
              <motion.p
                key="hunt"
                initial={{ opacity: 0, y: 6 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: -6 }}
                transition={{ duration: 0.22 }}
                className="mt-2 text-sm text-[var(--grav-dim)] leading-relaxed"
              >
                You’re scanning like it’s a menu. That’s normal. This won’t reward it.
              </motion.p>
            )}

            {mode === "impatient" && (
              <motion.p
                key="impatient"
                initial={{ opacity: 0, y: 6 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: -6 }}
                transition={{ duration: 0.22 }}
                className="mt-2 text-sm text-[var(--grav-dim)] leading-relaxed"
              >
                You’re trying to “find the point”. The point is: your product is probably
                explaining too much.
              </motion.p>
            )}
          </AnimatePresence>

          <div className="mt-6 grid grid-cols-1 sm:grid-cols-2 gap-3">
            <Card
              title={mode === "impatient" ? "Less copy. More consequence." : "Small changes. Real outcomes."}
              body={
                mode === "impatient"
                  ? "If you need to convince, the system is already leaking."
                  : "I work on flows that make decisions easier without begging."
              }
            />
            <Card
              title={mode === "hunting" ? "Stop browsing. Start feeling." : "Friction is a tool."}
              body={
                mode === "hunting"
                  ? "If this annoyed you, good. That’s the same signal your customers get."
                  : "You can remove friction, or you can move it to the right place."
              }
            />
          </div>
        </div>
      </div>
    </div>
  );
}

function Card({ title, body }: { title: string; body: string }) {
  return (
    <div className="rounded-2xl border border-white/10 bg-black/10 p-4">
      <div className="text-sm font-semibold tracking-tight">{title}</div>
      <div className="mt-1 text-xs leading-relaxed text-[var(--grav-dim)]">{body}</div>
    </div>
  );
}
